    export default {
        data() {
            return {
                all: false,    // 详情
                allNum: [],    //  判断呼叫中=>是点击的哪个选项中的详情按钮
                shopName: ["全部","卫生打扫","杯子","换麦克风","纸巾","开水","寄取酒","点单","碗筷","其他"],
                show: false,  // 判断筛选弹窗显示或隐藏
                company_name: "",  // 类型
                showtime: false,   // 显示自定义事件弹窗
                startTime: null,  //  开始时间
                value: null,   //  默认显示昨天
                dataTime: null,   //  初始时间
//                人员列表
                nameList: [],
                id: null,   //  人员ID
                type_id: 0,
            }
        },
        created(){
            this.value = this.$route.params.value;
            this.dataTime = this.$route.params.time;
            this.startTime = this.$route.params.time;
            this.id = this.$route.query.id;
            this.getInfo();  //  数据初始化
        },
        methods:{
            getInfo(){
                this.nameList = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getServiceTimeList,
                    type: "post",
                    data: {
                        user_id: this.id,
                        date_time: this.startTime,
                        type_id: this.type_id
                    },
                    success: (response, allResponse) => {
                        console.log('response',response);
                        this.nameList = this.nameList.concat(response)
                    }
                });
            },
            // 呼叫服务=>已解决详情
            overSolve(item){
                this.all = !this.all;
                this.allNum = item.data
            },
            //            自定义弹窗=>点击确定
            onConfirm(){
                this.dataTime = this.startTime;
                this.getInfo();
            },
            //            点击筛选触发
            navRigth(){
                console.log("我要筛选了")
                this.show = true;
            },
            //            筛选弹窗确定按钮
            choice(){
                this.show = false;
                this.shopName.forEach((item,index)=>{
                    if(this.company_name == item){
                        this.type_id = index
                    }
                });
                if(this.type_id > 6){
                    this.type_id = this.type_id + 1
                }
                this.getInfo();
            },
            //            选择时间
            date(index){
                this.value = index
                if(index == 0){
                    this.startTime = this.common.commonFun.get_date(1);
                    this.getInfo();
                }
                if(index == 1){
                    this.startTime = this.common.commonFun.get_date(0);
                    this.getInfo();
                }
                if(index == 2){
                    this.showtime = true;
                }
            }
        }
    }



